import React from 'react';
import { Card, Select, Table } from 'antd';
import { connect } from "react-redux";

import UserBox from "../../components/UserBox";
import * as scActionCreator from "../seleted/store/actionCreators";

const Option = Select.Option;

class Schedule extends React.Component {

    componentWillMount() {
        this.props.initSClist(this.props.id);
    }

    render() {
        var table = new Array(7);
        for (let i = 0; i < 12; i++) {
            table[i] = new Array(12);
        }
        this.props.selected.forEach(v=>{
            for (let i = v.time; i < v.acount; i++) {
                table[v.date] = {
                    space: v.space,
                    name: v.name,
                }
            }
        });
        console.log(table);
        return (
            <div>
                <UserBox/>
                <Card>
                    {this.renderTable}
                </Card>
            </div>
        );
    }

}

const mapStateToProps = (state) => {
    return{
        schedule: state.getIn(['schedule','schedule']),
        userinfo: state.getIn(['header','userinfo']),
        selected: state.get('selected'),
        id: state.getIn(['logined','id'])
    }
};

const mapDispatch = (dispatch) => ({
    initSClist(id){
        dispatch(scActionCreator.intiSelectedList(id));
    }
});

export default connect(mapStateToProps, mapDispatch) (Schedule);